import { reactive, readonly } from 'vue';
import api from '@/store/modules/warpnet/actions';

const cacheMap = reactive(new Map());

const ttlMs = 60_000; // 60 sec

export const reactiveCache = {
    state: readonly(cacheMap),

    clear() {
        cacheMap.clear();
    },

    invalidate(prefix) {
        for (const key of cacheMap.keys()) {
            if (key.startsWith(prefix)) {
                cacheMap.delete(key);
            }
        }
    },

    async getProfile(userId) {
        const userExpirationKey = `user::expiration`

        const expiration = cacheMap.get(userExpirationKey) || { createdAt: 0 };
        if (expiration && Date.now() - expiration.createdAt > ttlMs) {
            this.invalidate(`user`);
            cacheMap.set(userExpirationKey, { createdAt: Date.now() });
        }

        const cacheKey = `user::${userId}`;
        if (cacheMap.has(cacheKey)) {
            return cacheMap.get(cacheKey);
        }
        const result = await api.getProfile({}, userId);
        cacheMap.set(cacheKey, result);
        return result;
    },

    async getUsers({userId, limit, cursor}) {
        const cacheKey = `users::${userId}::${limit}::${cursor}`;
        if (cacheMap.has(cacheKey)) return cacheMap.get(cacheKey);
        const result = await api.getUsers({}, {userId:userId, limit:limit, cursor:cursor});
        cacheMap.set(cacheKey, result);
        return result;
    },

    async uploadImage(imgFile) {
        const key = await api.uploadImage({}, imgFile);
        const userId = sessionStorage.getItem("ownerUserId");
        const cacheKey = `image::${userId}::${key}`;
        cacheMap.set(cacheKey, imgFile);
        return key;
    },

    async getImage({userId, key}) {
        const cacheKey = `image::${userId}::${key}`;
        if (cacheMap.has(cacheKey)) return cacheMap.get(cacheKey);
        const result = await api.getImage({}, {userId, key});
        cacheMap.set(cacheKey, result);
        return result;
    },

    async getMyTimeline({limit, cursor}) {
        const timelineExpirationKey = `timeline::expiration`

        const expiration = cacheMap.get(timelineExpirationKey) || { createdAt: 0 };
        if (expiration && Date.now() - expiration.createdAt > ttlMs) {
            this.invalidate(`timeline`);
            cacheMap.set(timelineExpirationKey, { createdAt: Date.now() });
        }

        const cacheKey = `timeline::${limit}::${cursor}`;
        if (cacheMap.has(cacheKey)) {
            return cacheMap.get(cacheKey);
        }
        const result = await api.getMyTimeline({}, {limit, cursor});
        cacheMap.set(cacheKey, result);
        return result;
    },

    async getTweets({userId, limit, cursor}) {
        const cacheKey = `tweets::${userId}::${limit}::${cursor}`;
        if (cacheMap.has(cacheKey)) {
            return cacheMap.get(cacheKey);
        }
        const result = await api.getTweets({}, {userId, limit, cursor});
        cacheMap.set(cacheKey, result);
        return result;
    },

    async createTweet({text, imageKey}) {
        const tweet = await api.createTweet({}, {text, imageKey});
        this.invalidate(`timeline`);
        this.invalidate(`tweets::${tweet.user_id}`);
        const cacheKey = `tweet::${tweet.user_id}::${tweet.id}`;
        cacheMap.set(cacheKey, tweet)
        return tweet;
    },

    async deleteTweet({userId, tweetId}) {
        const result = await api.deleteTweet({}, {userId, tweetId});
        this.invalidate(`timeline`);
        this.invalidate(`tweets::${userId}`);
        cacheMap.delete(`tweet::${userId}::${tweetId}`)
        return result;
    },

    async getTweet({userId, tweetId}) {
        const cacheKey = `tweet::${userId}::${tweetId}`;
        if (cacheMap.has(cacheKey)) return cacheMap.get(cacheKey);
        const result = await api.getTweet({}, {userId, tweetId});
        cacheMap.set(cacheKey, result);
        return result;
    },

    async followUser(profileId) {
        const result = await api.followUser({}, profileId);
        const cacheKey = `isFollowed::${profileId}`;
        cacheMap.set(cacheKey, {})
        this.invalidate(`followers::${profileId}`);
        this.invalidate(`followees::${profileId}`);
        return result;
    },

    async isFollowed(profileId) {
        const cacheKey = `isFollowed::${profileId}`;
        return cacheMap.has(cacheKey)
    },

    async isFollower(profileId) {
        const cacheKey = `isFollower::${profileId}`;
        return cacheMap.has(cacheKey)
    },

    async unfollowUser(profileId) {
        const result = await api.unfollowUser({}, profileId);
        cacheMap.delete(`isFollowed::${profileId}`);
        this.invalidate(`followers::${profileId}`);
        this.invalidate(`followees::${profileId}`);
        return result;
    },

    async getFollowers({userId, limit, cursor}) {
        const cacheKey = `followers::${userId}::${limit}::${cursor}`;
        if (cacheMap.has(cacheKey)) return cacheMap.get(cacheKey);
        const followers = await api.getFollowers({}, {userId, limit, cursor});
        cacheMap.set(cacheKey, followers);
        for (const f of followers || []) {
            const followerKey = `isFollower::${f.follower}`;
            cacheMap.set(followerKey, {})
        }
        return followers;
    },

    async getFollowees({userId, limit, cursor}) {
        const cacheKey = `followees::${userId}::${limit}::${cursor}`;
        if (cacheMap.has(cacheKey)) return cacheMap.get(cacheKey);
        const result = await api.getFollowing({}, {userId, limit, cursor});
        cacheMap.set(cacheKey, result);
        return result;
    },

    async getTweetStats({tweetId, limit, cursor}) {
        const cacheKey = `tweetstats::${tweetId}::${limit}::${cursor}`;
        if (cacheMap.has(cacheKey)) return cacheMap.get(cacheKey);
        const result = await api.getTweetStats({}, {tweetId, limit, cursor});
        cacheMap.set(cacheKey, result);
        return result;
    },

    // create reply
    async replyTweet({rootId, parentId, parentUserId, text}) {
        const reply = await api.replyTweet(
            {}, {rootId, parentId, parentUserId, text},
            );
        const cacheKey = `reply::${rootId}::${reply.id}`;
        cacheMap.set(cacheKey, reply);
        this.invalidate(`tweetstats::${rootId}`)
        return reply;
    },

    async getReplies({rootId, parentId, limit, cursor}) {
        const cacheKey = `replies::${rootId}::${parentId}::${limit}::${cursor}`;
        if (cacheMap.has(cacheKey)) return cacheMap.get(cacheKey);
        const result = await api.getReplies({}, {rootId, parentId, limit, cursor});
        cacheMap.set(cacheKey, result);
        return result;
    },

    async getReply({rootId, replyId}) {
        const cacheKey = `reply::${rootId}::${replyId}`;
        if (cacheMap.has(cacheKey)) return cacheMap.get(cacheKey);
        const result = await api.getTweetReply({}, {rootId, replyId});
        cacheMap.set(cacheKey, result);
        return result;
    },

    async deleteReply({userId, rootId, replyId}) {
        const result = await api.deleteTweetReply({}, {userId, rootId, replyId});
        const cacheKey = `reply::${rootId}::${replyId}`;
        cacheMap.delete(cacheKey);
        this.invalidate(`tweetstats::${rootId}`)
        return result;
    },

    async likeTweet(tweetId) {
        const result = await api.likeTweet({}, tweetId);
        this.invalidate(`tweetstats::${tweetId}`)
        return result;
    },

    async unlikeTweet(tweetId) {
        const result = await api.unlikeTweet({}, tweetId);
        this.invalidate(`tweetstats::${tweetId}`)
        return result;
    },

    async retweetTweet({tweetId, userId, username, text}) {
        const result = await api.retweetTweet({}, {tweetId, userId, username, text});
        this.invalidate(`tweetstats::${tweetId}`)
        return result;
    },

    async unretweetTweet(tweetId) {
        const result = await api.unretweetTweet({}, tweetId);
        this.invalidate(`tweetstats::${tweetId}`)
        return result;
    },
};
